import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//页面
import { ExamApproCompComponent } from './examApproComp.component';//工作流
import {CustomWorkflowModule} from './custom/flow-custom.module';
import {ProductWorkflowModule} from './product/flow-product.module';
import {ValuationWorkflowModule} from './valuation/flow-valuation.module';
import {CommonWorkflowModule} from './common/flow-common.module';

const routes: Routes = [
    {
        path: '',
        component: ExamApproCompComponent,
        children: [
            { path: 'custom-workflow', loadChildren: './custom/flow-custom.module#CustomWorkflowModule' },
            // { path: 'product-workflow', loadChildren: './product/flow-product.module#ProductWorkflowModule' },
            // { path: 'valuation-workflow', loadChildren: './valuation/flow-valuation.module#ValuationWorkflowModule' },
            // { path: 'common-workflow', loadChildren: './common/flow-common.module#CommonWorkflowModule' },
        ]
    },
];

export const ExamApproCompRouting:ModuleWithProviders= RouterModule.forChild(routes);
